@include lg {
    .footer__row-container {
        padding: 35px 20px 0;
    }
    .footer__contacts {
        display: block;
    }
    .footer__site-map {
        display: block;
    }
    .footer {
        font-size: 12px;

        a {
            font-size: 12px;
        }
    }
    .footer__social-list {
        display: block;
        margin-left: 25px;
    }
}

.footer {
    display: flex;
    flex-direction: column;

    color: white;

    a {
        color: white;

        &:hover {
            color: #ffbc21;
        }
    }


    .footer__row-container {
        background-color: #3f3f3f;

        .footer__row {
            display: flex;
            justify-content: space-between;

            width: 100%;
            max-width: 1170px;
            margin: 0 auto;

            .footer__site-map-headline,
            .footer__contacts-headline {
                font-size: 18px;
                font-family: "Gotham Pro";
                color: rgb(255, 255, 255);
                line-height: 1.111;
                margin: 0;
            }

            .footer__social-headline {
                font-size: 14px;
                font-family: "Gotham Pro";
                color: rgb(255, 255, 255);
                line-height: 1.429;
                margin: 0;
            }

            .footer__site-map-item {
                font-size: 12px;
                font-family: "Gotham Pro";
                color: rgb(224, 224, 224);
                line-height: 1.667;
                margin-bottom: 5px;
            }

            .footer__site-map-list:last-child {
                margin-left: 90px;
            }

            .footer__site-map-block {
                display: flex;
            }

            .footer__social {
                display: flex;

                .footer__social-list {

                    ul {
                        li {
                            a {
                                display: flex;
                                align-items: center;

                                margin-bottom: 10px;

                                &:hover {
                                    color: #ffbc21;
                                }

                                span {
                                    margin-left: 7px;
                                }
                            }
                        }
                    }
                }
            }
        }
        .footer__contacts-item {
            &:first-child {
                a {
                    font-size: 14px;
                    font-weight: 600;
                    span {
                        font-size: 12px;
                        font-weight: normal;
                    }
                }
            }
            margin-bottom: 5px;

            &:last-child {
                margin-top: 20px;
            }
        }

        .subs-container {
            .footer__subscribe {
                font-size: 18px;
                font-family: "Gotham Pro";
                color: rgb(255, 255, 255);
                line-height: 1.111;
                margin: 0;
                margin-bottom: 15px;
            }

            input {
                background-color: #3f3f3f;
                border: 0;
                color: white;
                padding: 7px 0;
                border-bottom: 1px solid white;
                outline:none;
                margin-right: 15px;
                margin-bottom: 30px;
                width: 180px;

                &::placeholder {
                    color: rgb(224, 224, 224);
                    font-family: "Gotham Pro";
                }
            }

            button {
                width: 130px;
                height: 30px;
                border: 0;
                border-radius: 4px;
                background-color: #00a3dd;

                font-size: 12px;
                font-family: "Gotham Pro Bold";
                color: rgb(255, 255, 255);
                text-transform: uppercase;
                cursor: pointer;
                outline: none;

                &:hover {
                    background-color: #01bcfe;
                }
                &:active {
                    background-color: #3f3f3f;

                }
            }
        }
    }

    .footer__container-copywrite {
        background-color: #333333;

        .footer__row-copy {
            box-sizing: border-box;
            padding-left: 10px;
            display: flex;
            font-size: 11px;
            line-height: 1.667;

            width: 100%;
            max-width: 1170px;
            margin: 0 auto;
        }
    }
}