@import "../../helpers/mq";

@include md {
    .about-us__container {
        margin: 100px auto;
    }
    .video-container {
        margin: 100px 0 0;
    }
    .services__item {
        width: 26%;
        margin-top: 50px;
    }
    .services__headline {
        font-size: 48px;
    }
    .about-us__headline {
        font-size: 48px;
    }
    .slider {
        height: 480px;
    }
    .all-inclusive__city {
        font-size: 17px;
    }
    .about-us__text {
        font-size: 18px;
    }
    .about-us__list {
        justify-content: center;
    }
    .about-us__item {
        max-width: 800px;
    }
    .services {
        margin: 100px auto;
    }
    .services__text {
        font-size: 14px;
    }
    .all-inclusive__container {
        padding: 100px 15px;
    }
    .all-inclusive__price-text {
        font-size: 24px;
    }
    .discount {
        p {
            font-size: 16px;
        }
    }
    .all-inclusive__desctribe-text {
        font-size: 17px;
    }
    .all-inclusive__stars {
        font-size: 17px;
    }
    .all-inclusive__describe-headline {
        font-size: 22px;
    }
    .search-button {
        max-width: 160px;
    }
    .appartments-input {
        margin: 0 10px 0 0;
        width: 310px;
        p {
            font-size: 16px;
        }
    }
    .search__place {
        &::placeholder {
            font-size: 16px;
        }
    }
    .where {
        width: 42%;
    }
    .calendar-input {
        margin: 0 10px;
        width: 180px;
        p {
            font-size: 16px;
        }
    }
    .all-inclusive__describe-headline {
        height: auto;
        margin: 0;
        padding: 20px 0 0;
    }
    .all-inclusive__describe {
        margin: 0;
        padding: 0 20px;
    }
    .search__container {
        padding: 10px 20px;
    }
    .slider__container-search {
        padding-left: 20px;
    }
    .all-inclusive__headline {
        font-size: 48px;
        margin: 0 auto 50px 20px;
    }
    .all-inclusive__list {
        justify-content: space-around;
    }

    .all-inclusive__element:nth-child(3) {
        display: none;
    }



    .slider__line {
        width: 10px;
        height: 10vw;
    }

    .slider__headline-text {
        font-size: 9vw;
        margin: 0;
        margin-left: 30px;

        .slider__headline-top {
            font-size: 4vw;
        }
    }
    .slider__button-more {
        margin-top: 55px;
    }
}