@import "../../helpers/mq";

@include lg {
    .header__header-logo {
        flex-direction: column;
    }
    .button__menu-mobile {
        display: none;
    }
    .select-page {
        border-bottom: 3px solid #60c659;
    }

    .header__header-logo {
            align-items: center;
        }
        .header__navigation {
            width: 100%;

            display: flex;
            align-items: center;
            justify-content: space-between;
        }
        .site-navigation {
            display: flex;
            margin: 0 15px;
            min-width: 500px;
            justify-content: space-between;
        }

        .site-navigation a {
            padding-bottom: 10px;
            font-size: 14px;
        }
        .header__container {
            max-width: 1170px;
        }
        .header__logo-text {
            font-size: 14px;
        }
    }

.header__header-logo a {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
    .header__logo-text {
        font-family: "Gotham Pro";
        color: rgb(63, 63, 63);
        line-height: 1.286;
    }

    .site-navigation a {
        font-family: "Gotham Pro Bold";
        color: rgb(63, 63, 63);
        text-transform: uppercase;
        line-height: 1.286;

        &:hover {
            color: rgb(144, 144, 144);;
        }
    }
    .header__number-item {
        display: flex;
        align-items: center;
    }

    .header__number-item a {
        font-size: 14px;
        font-family: "Gotham Pro";
        color: rgb(63, 63, 63);
        text-transform: uppercase;
        line-height: 1.714;
        margin-left: 5px;
    }

    .header__number-item:first-child a {
        font-size: 16px;
        line-height: 1.5;
        margin-left: 0px;
        font-family: "Gotham Pro";
        font-weight: 600;
    }


    .header__number-item:first-child a span {
        font-size: 12px;
        font-family: "Gotham Pro";
        text-transform: none;
        line-height: 1;
        font-weight: 500;
    }


    .header__phone-logo:nth-child(2) {
        max-width: 20px;
    }

    .header__phone-logo:nth-child(3) {
        max-width: 17px;
    }

    .header {
        display: flex;
        justify-content: center;

        .header__container {
            width: 100%;
            padding: 15px 15px 3px;

            .header__navigation {

                .header__header-logo {
                    display: flex;
                    margin-left: 5px;


                    &:hover {
                        color: rgb(144, 144, 144);;
                    }

                    .header__logo-img {
                        max-width: 75px;
                    }
                    .header__logo-text {
                        text-align: center;
                        margin: 5px 0;
                    }
                }

                .site-navigation {
                    width: 100%;
                    max-width: 600px;
                }

                .header__numbers-list {
                    margin-top: 0;

                    .header__phone-logo {
                        width: 20px;
                    }
                }
            }
        }
    }