@include lg {
    .video-container {
        margin: 100px 0 0;
    }
}

.video {
    background-color: #f5fbfd;
    display: flex;
    justify-content: center;
    .video-container {
        width: 100%;
        max-width: 1170px;
        .video-file {
            width: 100%;
            max-height: 658px;
            height: (100vw / 16) * 9;
        }
    }
}