.slide-top {
    box-sizing: border-box;
    padding-right: 20px;
    max-width: 1330px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 20px;

    display: flex;
    justify-content: flex-end;

    .slide-top__container {
        width: 80px;
        height: 80px;
        border-radius: 50%;
        background-color: white;
        box-shadow: 0px 0px 25px 0px rgba(51, 51, 51, 0.15);

        display: flex;
        justify-content: center;
        align-items: center;

        &:hover {
            cursor: pointer;
            background-color: rgb(63, 63, 63);

            .slide-top__arrow {
                background: url("../img/slide-top/arrow-white.png") no-repeat;
            }
        }

        .slide-top__arrow {
            width: 32px;
            height: 20px;
            background: url("../img/slide-top/arrow-gray.png") no-repeat;
        }
    }
}