@include smL {
    .footer__row-container {
        padding: 35px 20px 0;
    }
    .footer__social-list {
        display: none;
    }
    .footer__contacts {
        display: block;
    }
    .footer__site-map {
        display: block;
    }
    .footer__social-list {
        margin-left: 25px;
    }

}

@include sm {
    .footer__row-container {
        padding: 25px 10px 30px 10px;
    }
    .footer__social {
        width: 100%;
        justify-content: space-around;
    }
    .footer__social-list {
        margin-left: 0;
    }
    .footer__contacts {
        display: none;
    }
    .footer__site-map {
        display: none;
    }
    .footer__social-list {
        display: block;
    }
    .footer {
        font-size: 12px;

        a {
            font-size: 13px;
        }
    }


}