@mixin sm {
    @media (max-width: 575px){
        @content;
    }
}

@mixin smL {
    @media (min-width: 576px) and (max-width: 899px){
        @content;
    }
}

@mixin md {
    @media (min-width: 900px) and (max-width: 1199px){
        @content;
    }
}

@mixin lg {
    @media screen and (min-width: 1200px){
        @content;
    }
}