@include lg {
    .all-inclusive__container {
        width: 100%;
        padding: 100px 15px;
    }
    .all-inclusive__city {
        font-size: 14px;
    }
    .all-inclusive__price-text {
        font-size: 22px;
    }
    .discount {
        p {
            font-size: 14px;
        }
    }
    .all-inclusive__desctribe-text {
        font-size: 14px;
    }
    .all-inclusive__stars {
        font-size: 15px;
    }
    .all-inclusive__describe-headline {
        font-size: 19.5px;
    }
    .all-inclusive__element {
        display: flex;
    }
    .all-inclusive__list {
        justify-content: space-between;
    }
    .all-inclusive__inner-container {
        padding: 20px;
    }
    .all-inclusive__headline {
        font-size: 48px;
        margin: 0 auto 50px 15px;
    }

    .all-inclusive__describe {
        margin: 0;
        padding: 0 20px;
    }
    .all-inclusive__describe-headline {
        height: 50px;
    }
}

.all-inclusive {
    background-color: #f5fbfd;

    .all-inclusive__container {
        box-sizing: border-box;
        max-width: 1330px;
        width: 100%;
        margin: 0 auto;

        display: flex;
        flex-direction: column;

        .all-inclusive__headline {
            font-family: "Gotham Pro Light" ;
            color: rgb(63, 63, 63);
            line-height: 0.375;

            max-width: 1170px;
        }

        .all-inclusive__inner-container {

            display: flex;
            align-items: center;
            justify-content: space-between;

            .all-inclusive__left {
                width: 20px;
                height: 32px;
                padding-right: 20px;
                background: url(../img/all-inclusive/arrow-left.png) no-repeat center;

                &:hover {
                    background: url(../img/all-inclusive/left-orange.png) no-repeat center;
                    cursor: pointer;
                }
            }

            .all-inclusive__right {
                width: 20px;
                height: 32px;
                padding-left: 20px;
                background: url(../img/all-inclusive/arrow-right.png) no-repeat center;

                &:hover {
                    background: url(../img/all-inclusive/right-orange.png) no-repeat center;
                    cursor: pointer;
                }
            }

            .all-inclusive__slider {
                width: 100%;
                max-width: 1170px;
                position: relative;
                overflow: hidden;
            }

            .all-inclusive__list {
                width: 100%;
                margin-left: auto;
                margin-right: auto;

                display: flex;

                .all-inclusive__element {
                    box-sizing: border-box;
                    flex-direction: column;
                    border: 1px solid #e7e7e7;
                    max-width: 370px;

                    &:hover {
                        box-shadow: 0px 0px 25px 0px rgba(63, 63, 63, 0.15);
                    }

                    .all-inclusive__image {
                        position: relative;
                        height: 200px;
                        max-width: 370px;
                        width: 100%;
                        z-index: 1;

                        .discount {
                            margin: 20px;
                            position: absolute;
                            z-index: 2;
                            padding: 5px 10px;
                            border-radius: 5px;
                            border-bottom-left-radius: 0;
                            background: linear-gradient( 0deg, rgb(255,127,30) 0%, rgb(255,188,33) 100%);

                            p {
                                font-family: "Gotham Pro Bold";
                                color: rgb(255, 255, 255);
                                line-height: 1.429;
                                margin: 0;
                            }
                        }
                    }

                    .all-inclusive__price {
                        display: flex;
                        align-items: center;
                        padding: 0 20px;

                        background-color: #00a3dd;

                        .all-inclusive__price-text {
                            font-family: "Gotham Pro Bold";
                            color: rgb(255, 255, 255);
                            line-height: 0.909;
                            margin-left: 10px;
                        }
                    }

                    .all-inclusive__describe {
                        box-sizing: border-box;
                        background-color: white;

                        .all-inclusive__describe-headline {
                            font-family: "Gotham Pro";
                            color: rgb(63, 63, 63);
                            line-height: 1.4;
                        }

                        .all-inclusive__desctribe-text {
                            font-family: "Gotham Pro";
                            color: rgb(90, 90, 90);
                            line-height: 1.571;
                            min-height: 80px;
                        }
                        .all-inclusive__stars {
                            display: flex;
                            align-items: center;
                            height: 30px;

                            font-family: "Gotham Pro";
                            color: rgb(255, 188, 33);
                            font-weight: 600;
                            line-height: 1.429;
                            margin: 0;

                            img {
                                margin: -3px 0 0 4px;
                            }
                        }

                        .all-inclusive__reviews {
                            box-sizing: border-box;
                            display: flex;
                            align-items: center;
                            padding-bottom: 10px;

                            .all-inclusive__assessment {
                                background-color: #60c659;
                                width: 40px;
                                height: 30px;
                                border-radius: 7px;
                                border-bottom-left-radius: 0px;
                                display: flex;
                                justify-content: center;
                                align-items: center;

                                .all-inclusive__assessment-text {
                                    font-size: 14px;
                                    font-family: "Gotham Pro Bold";
                                    color: rgb(255, 255, 255);
                                    line-height: 1.429;
                                }
                            }

                            .all-inclusive__reviews-text {
                                font-size: 14px;
                                font-family: "Gotham Pro Bold";
                                color: rgb(96, 198, 89);
                                line-height: 1.429;
                                margin: auto 10px;
                            }

                            .all-inclusive__quantity {
                                font-size: 13px;
                                font-family: "Gotham Pro";
                                color: rgb(114, 114, 114);
                                line-height: 1.667;
                                margin: auto 10px;
                            }
                            .all-inclusive__like-icon {
                                margin-top: -6px;
                            }
                        }
                    }
                }
            }
        }
    }
}

.all-inclusive_second {
    background-color: white;

    .all-inclusive__container {
        padding-bottom: 10px;
    }

    .all-inclusive__city {
        font-family: "Gotham Pro";
        color: rgb(114, 114, 114);
        line-height: 1.429;
    }
}