    .all-inclusive__element:nth-child(3) {
        display: none;
    }



    .slider__line {
        width: 8px;
        height: 13vw;
    }

    .slider__headline-text {
        font-size: 10vw;
        margin-top: 0;
        margin-left: 30px;

        .slider__headline-top {
            font-size: 6vw;
        }
    }
    .services__item {
        width: 26%;
        margin-top: 50px;
    }
    .video-container {
        margin: 100px 0 0;
    }
}

