@include lg {
    .slider {
        height: 480px;
    }
    .slider__line {
        width: 10px;
        height: 125px;
    }

    .slider__headline-text {
        font-size: 120px;
        margin: 0;
        margin-left: 30px;

        .slider__headline-top {
            font-size: 48px;
        }
    }
    .slider__container {
        margin-left: auto;
        margin-right: auto;
    }

    .slider__container-search {
        margin-left: auto;
        margin-right: auto;
    }
    .slider__button-more {
        margin-top: 55px;
    }
}








.slider {

    box-sizing: border-box;
    background-image: url("../img/slider/slider-background.jpg");
    background-position: 0 10%;
    background-repeat: no-repeat;
    background-size: cover;

    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .slider__container-search {
        box-sizing: border-box;
        width: 100%;
        max-width: 1170px;

        .slider__search {
            box-sizing: border-box;
            background-color: rgb(96, 198, 89);
            width: 235px;
            height: 40px;
            border-top-left-radius: 5px;
            border-top-right-radius: 5px;
            padding-top: 1px;
            padding-bottom: 10px;

            font-size: 16px;
            font-family: "Gotham Pro";
            color: rgb(255, 255, 255);
            font-weight: 600;
            line-height: 1.333;
            text-align: center;
        }
    }

    .slider__container {
        box-sizing: border-box;
        max-width: 1330px;
        width: 100%;
        padding: 0 15px;
        margin-top: 70px;

        display: flex;
        align-items: center;

        .slider__arrow-left {
            width: 20px;
            height: 32px;
            background: url(../img/slider/arrow-left.png) no-repeat center;

            &:hover {
                background: url(../img/all-inclusive/left-orange.png) no-repeat center;
                cursor: pointer;
            }
        }

        .slider__arrow-right {
            width: 20px;
            height: 32px;
            background: url(../img/slider/arrow-right.png) no-repeat center;

            &:hover {
                background: url(../img/all-inclusive/right-orange.png) no-repeat center;
                cursor: pointer;
            }
        }

        .slider__content {
            width: 100%;
            max-width: 1170px;
            margin-left: 30px;
            margin-right: 30px;
            justify-self: center;

            display: flex;
            flex-direction: column;

            .slider__button-more {
                max-width: 270px;
                border-style: solid;
                border-width: 2px;
                border-color: rgb(255, 188, 33);
                border-radius: 5px;
                background-color: transparent;

                font-size: 16px;
                font-family: "Gotham Pro Bold";
                color: rgb(255, 188, 33);
                text-transform: uppercase;
                line-height: 1.5;
                padding: 15px 0 12px;

                &:hover {
                    background: linear-gradient( 0deg, rgb(255,127,30) 0%, rgb(255,188,33) 100%);
                    color: white;
                    cursor: pointer;
                }
            }

            .slider__headline {
                display: flex;

                .slider__line {
                    margin-top: 16px;

                    background-color: white;
                }


                .slider__headline-text {
                    font-family: "Gotham Pro Bold";
                    color: rgb(255, 255, 255);
                    text-transform: uppercase;
                    line-height: 0.6;

                    .slider__headline-top {
                        font-family: "Gotham Pro Bold";
                        color: rgb(255, 255, 255);
                        line-height: 1.5;
                        margin: 0;
                        margin-bottom: 10px;
                    }
                }
            }
        }
    }
}