@include md {
    .header__header-logo {
        flex-direction: column;
    }
    .button__menu-mobile {
        display: none;
    }
    .select-page {
        border-bottom: 3px solid #60c659;
    }

    .header__header-logo {
        align-items: center;
    }
    .site-navigation a {
        padding-bottom: 10px;
        font-size: 14px;
    }
    .header__numbers-list {
        display: none;
    }
    .site-navigation {
        display: flex;
        margin: 0 15px;
        min-width: 500px;
        justify-content: space-between;
    }
    .header__navigation {
        width: 100%;

        display: flex;
        align-items: center;
        justify-content: space-between;
    }
}