@include lg {
    .services__text {
        font-size: 14px;
    }
    .services__headline {
        font-size: 48px;
    }
    .services {
        margin: 100px auto;
    }
    .services__item {
        width: 26%;
        margin-top: 50px;
    }
}


.services {
    width: 100%;
    max-width: 1170px;

    .services__headline {
        padding: 0 0 0 20px;
        font-family: "Gotham Pro Light" ;
        color: rgb(63, 63, 63);
        line-height: 0.375;
        margin: 0 auto;
    }

    .services__list {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;

        .services__item {

            display: flex;
            flex-direction: column;
            align-items: center;

            .services__text {
                font-family: "Gotham Pro";
                color: rgb(63, 63, 63);
                line-height: 1.714;
                font-weight: 600;
                text-align: center;
            }
        }
    }
}