@import "../../helpers/mq";

@include smL {
    .button__menu-mobile {
        display: none;
    }
    .header__header-logo {
        align-items: center;
    }
    .header__navigation {
        width: 100%;

        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    .header__numbers-list {
        display: none;
    }
    .site-navigation a {
        font-size: 13px;
        padding-bottom: 10px;
    }
    .site-navigation {
        display: flex;
        box-sizing: border-box;
        min-width: 300px;
        justify-content: space-around;
        padding-left: 20px;
    }
    .header__logo-text {
        display: none;
    }
    .select-page {
        border-bottom: 3px solid #60c659;
    }
    .header__header-logo {
        flex-direction: column;
    }

}

@include sm {
    .header__header-logo {
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
    }
    .button__menu-mobile {
        border: 1px solid #60c659;
        padding: 10px 15px;
        color: #60c659;
        font-weight: bold;
        border-radius: 10px;
        font-size: 6vw;
        background-color: transparent;
        cursor: pointer;
        margin: auto 5px;
        display: block;
        &,
        &:active,
        &:focus {
            outline: none;
        }
    }
    .select-page {
        border-bottom: 0px solid #60c659;
    }

    .header__header-logo {
        align-items: flex-start;
    }
    .header__navigation {
        width: 100%;
    }

    .header__numbers-list {
        display: none;
    }
    .site-navigation a {
        font-size: 22px;
        padding-bottom: 0;
    }
    .site-navigation {
        display: none;

        margin: 0 auto;
        flex-direction: column;
        text-align: center;
        box-sizing: border-box;
        min-width: 300px;
        padding-left: 0;
    }
    .site-navigationON {
        display: flex;
    }
    .site-navigation li {
        padding: 10px;
        border-bottom: 1px solid #d4d2d2;

        &:first-child {
            margin-top: 15px;
            border-top: 1px solid #d4d2d2;

        }

        &:last-child {
            padding-bottom: 7px;
            border-bottom: 0 solid #d4d2d2;
        }
    }
    .header__logo-text {
        display: none;
    }

}