@include lg {
    .about-us__text {
        font-size: 14px;
    }
    .about-us__item {
        max-width: 520px;
    }
    .about-us__list {
        justify-content: space-between;
    }
    .about-us__headline {
        font-size: 48px;
    }
    .about-us__container {
        margin: 100px auto;
    }
}

.about-us {
    background-color: #f5fbfd;
    padding: 10px 0;
    margin: 0;

    .about-us__headline {
        font-family: "Gotham Pro Light" ;
        color: rgb(63, 63, 63);
        line-height: 0.375;
        margin: 0 auto 50px 20px;
    }

    .about-us__container {
        width: 100%;
        max-width: 1170px;

        .about-us__list {
            display: flex;
            flex-wrap: wrap;

            .about-us__item {
                min-height: 110px;
                margin-bottom: 20px;

                display: flex;
                align-items: center;
                justify-content: center;

                .about-us__text {
                    margin-left: 20px;
                    font-family: "Gotham Pro";
                    color: rgb(114, 114, 114);
                    font-weight: 600;
                    line-height: 1.714;
                }

                div {
                    min-width: 70px;
                    text-align: center;

                    img {
                        margin-right: auto;
                        margin-left: auto;
                    }
                }
            }
        }
    }
}