@include lg {
    .search-button {
        max-width: 160px;
    }
    .search__container {
        padding: 10px 0;
    }
    .search__place {
        &::placeholder {
            font-size: 14px;
        }
    }
    .calendar-input {
        margin: 0 10px;
        width: 180px;
        p {
            font-size: 14px;
        }
    }
    .appartments-input {
        margin: 0 10px 0 0;
        width: 310px;

        p {
            font-size: 14px;
        }
    }
    .where {
        width: 42%;
    }
}

.search {
    background-color: rgb(96, 198, 89);

    .search__container {
        box-sizing: border-box;
        width: 100%;
        max-width: 1170px;
        margin: 0 auto;
        display: flex;

        .where {
            position: relative;
            .search__place {
                box-sizing: border-box;
                border: 0;
                width: 100%;
                border-radius: 5px;
                padding: 18px 0 18px 45px;

                background-image: url('../img/search/gps-icon.png');
                background-repeat: no-repeat;
                background-position: 20px;
                outline: none;

                &::placeholder {
                    color: #d4d2d2;
                    font-family: "Gotham Pro";
                }
            }

            .place-dropdown {
                display: none;

                z-index: 5;
                margin-top: 5px;
                position: absolute;
                background-color: white;
                width: 100%;
                border-radius: 5px;
                box-shadow: 0px 0px 25px 0px rgba(63, 63, 63, 0.15);

                ul {
                    margin: 0;
                    li {
                        padding: 15px;

                        &:hover {
                            background-color: #ebebeb;
                            cursor: pointer;
                        }
                    }
                }
            }
            .displayOn {
                display: block;
            }
        }
        .calendar {
            position: relative;

            .calendar-input {
                cursor: pointer;
                box-sizing: border-box;
                padding: 18px;
                height: 55px;
                background-color: white;
                border-radius: 5px;

                display: flex;
                align-items: center;

                p {
                    margin: 5px 2px 0 ;
                    color: #d4d2d2;
                }

                .inputDate {
                    margin-left: 10px;
                }
            }

            .calendar-dropdown {
                width: 541px;
                box-sizing: border-box;
                background-color: white;
                border-radius: 5px;
                box-shadow: 0px 0px 25px 0px rgba(63, 63, 63, 0.15);
                position: absolute;
                z-index: 4;
                margin: 5px 10px;
                padding: 20px;

                display: flex;
                flex-direction: column;

                .top {
                    border-bottom: 1px solid #ebebeb;
                    p {
                        text-align: center;
                        margin-top: 0;
                        font-size: 14px;
                        font-family: "Gotham Pro";
                        color: rgb(114, 114, 114);
                        line-height: 1.714;
                    }
                }

                .main-block {
                    display: flex;
                    justify-content: space-between;

                    .calendar-block {

                        .month-name {
                            display: flex;
                            align-items: center;

                            img {
                                cursor: pointer;
                            }

                            h2 {
                                margin-left: auto;
                                margin-right: auto;
                                font-size: 16px;
                                font-family: "Gotham Pro";
                                color: rgb(63, 63, 63);
                                line-height: 1.5;
                            }
                        }

                        .day-names {
                            display: flex;
                            margin: 0;
                            li {
                                width: 35px;
                                height: 35px;
                                text-align: center;
                                font-size: 14px;
                                font-family: "Gotham Pro";
                                color: rgb(135, 135, 135);
                                line-height: 1.714;
                            }
                        }
                        .day-nums {
                            display: flex;
                            flex-wrap: wrap;
                            margin: 0;


                            li {
                                box-sizing: border-box;
                                padding: 9px;
                                width: 35px;
                                height: 35px;
                                text-align: center;
                                font-size: 14px;
                                font-family: "Gotham Pro";
                                color: rgb(63, 63, 63);
                                line-height: 1.714;
                                cursor: pointer;

                                &:hover {
                                    background-color: #ebebeb;
                                }
                            }

                            .first-day {
                                background-color: #ff7f1e;
                                color: white;
                            }

                            .between-day {
                                background-color: #ebebeb;
                            }

                            .last-day {
                                background-color: #cfcfcf;
                            }
                        }
                    }
                }
            }
            .displayNone {
                display: none;
            }
        }
        .appartments {
            margin: 0;
            .appartments-input {
                cursor: pointer;
                box-sizing: border-box;
                padding: 18px;
                height: 55px;
                background-color: white;
                border-radius: 5px;

                display: flex;
                align-items: center;

                p {
                    margin: 5px 0 0 5px;
                    color: #d4d2d2;
                }
            }
        }
        .displayNone {
            display: none;
        }
        .appartments-dropdown {
            position: absolute;
            width: 100%;
            max-width: 310px;
            background-color: white;
            margin-top: 5px;
            border-radius: 5px;
            z-index: 5;
            padding: 0;
            box-shadow: 0px 0px 25px 0px rgba(63, 63, 63, 0.15);

            .appartments-dropdown-container {
                padding: 20px;
                margin: 0;

                ul {
                    margin: 0;

                    li {
                        display: flex;
                        justify-content: space-between;

                        .appartments__drop-text {
                            font-size: 14px;
                            font-family: "Gotham Pro";
                            color: rgb(135, 135, 135);
                            line-height: 1.714;
                        }

                        .appartments__counter {
                            display: flex;
                            width: 120px;
                            justify-content: space-between;

                            .appartments__button {
                                display: flex;
                                justify-content: center;
                                align-items: center;
                                width: 40px;
                                height: 40px;
                                box-sizing: border-box;
                                border: 1px solid rgb(135, 135, 135);
                                border-radius: 5px;
                                background-color: transparent;
                                cursor: pointer;
                            }

                            .plus {
                                border-color: #60c659;

                                span {
                                    margin-top: 3px;
                                    font-weight: bold;
                                    color: #60c659;
                                }

                                &:hover {
                                    background-color: #60c659;

                                    span {
                                        color: white;
                                    }
                                }
                            }
                            .minus {
                                border-color: rgb(135, 135, 135);

                                span {
                                    margin-top: 3px;
                                    font-weight: bold;
                                    color: rgb(135, 135, 135);
                                }

                                &:hover {
                                    background-color: rgb(135, 135, 135);

                                    span {
                                        color: white;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }

        .search-button {
            width: 100%;
            border-radius: 5px;
            background: linear-gradient( 0deg, rgb(255,127,30) 0%, rgb(255,188,33) 100%);
            border: 0;

            font-size: 14px;
            font-family: "Gotham Pro Bold";
            color: rgb(255, 255, 255);
            text-transform: uppercase;
            cursor: pointer;

            &:hover {
                background: #3f3f3f;
            }
        }
    }
}